import React from 'react'
import {
    Accordion,
    AccordionSummary,
    AccordionDetails,
    Typography,
    Grid,
    Container,
    Avatar,
    List,
    ListItemAvatar,
    ListItemText,
    ListItem,
    Paper,
    Button,
    Box

} from '@mui/material';
import AuthContext from '../../../provider/AuthProvider';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import HomeIcon from '@mui/icons-material/Home';
import NumbersIcon from '@mui/icons-material/Numbers';
import PhoneIcon from '@mui/icons-material/Phone';
import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import WcIcon from '@mui/icons-material/Wc';
import CakeIcon from '@mui/icons-material/Cake';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';
import DateRangeIcon from '@mui/icons-material/DateRange';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import MedicalServicesIcon from '@mui/icons-material/MedicalServices';
import FindInPageIcon from '@mui/icons-material/FindInPage';
import ScienceIcon from '@mui/icons-material/Science';
import CoronavirusIcon from '@mui/icons-material/Coronavirus';
import MedicationIcon from '@mui/icons-material/Medication';
import DownloadIcon from '@mui/icons-material/Download';
import { blue, red, pink, purple, green } from '@mui/material/colors';
import { formatDate, fullname, getYearSection } from '../../../utils/Utilities';
import { useNavigate } from "react-router-dom";
const colors = {
    blue: blue[500],
    red: red[500],
    pink: pink[500],
    purple: purple[500],
    green: green[500],
    text: 'white'
}
const ViewUser = ({ medicalRecord, user }) => {
    const { loggedInUser } = React.useContext(AuthContext);
    const allowed = ['doctor', 'dentist', 'nurse'];
    const navigate = useNavigate();
    if (Object.entries(user).length !== 0) {
        const medicalRecordExist = Object.entries(medicalRecord).length !== 0;
        const basicInfo = [
            {
                text: user?.doc?.employee_no || user?.doc?.user_no,
                icon: <NumbersIcon />
            },
            {
                text: user?.doc?.civil_status,
                icon: <AccountCircleIcon />
            },
            {
                text: user?.doc?.sex.toUpperCase(),
                icon: <WcIcon />
            },
            {
                text: user?.doc?.age,
                icon: <DateRangeIcon />
            },
            {
                text: formatDate(user?.doc?.birthday),
                icon: <CakeIcon />
            },
            {
                text: user?.doc?.user_id?.email,
                icon: <AlternateEmailIcon />
            }
        ]
        const downloadMedicalRecord = async (e) => {
            e.preventDefault();
            // QUICKFIX:DOWNLOAD MEDICAL RECORD
            window.open(`https://e-consultation-app.herokuapp.com/api/medical-records/${medicalRecord.doc._id}/export`, '_self');
            // https://stackoverflow.com/questions/41938718/how-to-download-files-using-axios
        }
        const downloadPrescriptionRecord = async (id) => {
            let host = 'https://e-consultation-app.herokuapp.com';
            window.open(`${host}/api/medical-records/${medicalRecord.doc._id}/prescriptions/${id}/export`, '_self');
        }
        if (!loggedInUser) {
            return 'Authenticating...'
        }
        return (
            <>
                <Container>
                    <Button
                        component="a"
                        startIcon={(<ArrowBackIcon fontSize="small" />)}
                        sx={{ mt: 3 }}
                        variant="contained"
                        onClick={() => navigate(-1)}
                    >
                        Go Back
                    </Button>
                    <Grid container spacing={3}>
                        <Grid item xs={12} >
                            <Avatar

                                alt={fullname(user?.doc?.name).toUpperCase()}
                                src={`/assets/img/${user?.doc?.photo}`}
                                sx={{
                                    width: 200,
                                    height: 200,
                                    bgcolor: blue[500],
                                    fontSize: 40,
                                    boxShadow: 3,
                                    margin: 'auto'
                                }}
                            />
                            <Typography variant="h4" gutterBottom component="div" align="center" sx={{ textTransform: 'capitalize' }}>
                                {fullname(user?.doc?.name).toUpperCase()}
                            </Typography>
                            {user.doc.user_id.role === 'student' ? (
                                <Typography variant="h5" gutterBottom component="div" align="center" sx={{ textTransform: 'capitalize' }}>
                                    {getYearSection(user.doc)}
                                </Typography>
                            ) : (
                                <Typography variant="h5" gutterBottom component="div" align="center" sx={{ textTransform: 'capitalize' }}>
                                    {user.doc.department_id.department}
                                </Typography>
                            )

                            }

                        </Grid>
                        <Grid item xs={12}>
                            {
                                allowed.includes(loggedInUser.user_id.role) ? (
                                    <Accordion >
                                        <AccordionSummary
                                            expandIcon={<ExpandMoreIcon />}
                                            aria-controls="panel1a-content"
                                            id="panel1a-header"

                                        >
                                            <List>
                                                <ListItem sx={{ padding: 0 }}>
                                                    <ListItemAvatar>
                                                        <Avatar>
                                                            <MedicalServicesIcon />
                                                        </Avatar>
                                                    </ListItemAvatar>
                                                    <ListItemText
                                                        primary={medicalRecordExist ? 'Medical Record' : "No Medical Record Found"}
                                                    />
                                                </ListItem>
                                            </List>
                                        </AccordionSummary>
                                        <AccordionDetails>
                                            {
                                                medicalRecordExist ? (
                                                    <Grid container spacing={2}>
                                                        <Grid item xs={12}>
                                                            <Button
                                                                variant="outlined"
                                                                color="primary"
                                                                onClick={downloadMedicalRecord}
                                                            >
                                                                <DownloadIcon />
                                                                Download Medical Record
                                                            </Button>
                                                        </Grid>
                                                        <Grid item xs={12} md={4}>
                                                            <Paper elevation={1} sx={{ padding: 3, backgroundColor: colors.blue, color: colors.text }}>
                                                                <Typography variant='h6'>
                                                                    <FindInPageIcon /> Diagnosis
                                                                </Typography>
                                                                {medicalRecord.doc.diagnosis.map((item, i) => (
                                                                    <Typography key={i}>
                                                                        - {item.diagnose}
                                                                    </Typography>
                                                                ))}
                                                            </Paper>
                                                        </Grid>
                                                        <Grid item xs={12} md={4}>
                                                            <Paper elevation={1} sx={{ padding: 3, backgroundColor: colors.blue, color: colors.text }}>
                                                                <Typography variant='h6'>
                                                                    <ScienceIcon />   Laboratories
                                                                </Typography>
                                                                {medicalRecord.doc.laboratories.map((item, i) => (
                                                                    <Typography key={i}>
                                                                        - {item.laboratory}
                                                                    </Typography>
                                                                ))}
                                                            </Paper>
                                                        </Grid>
                                                        <Grid item xs={12} md={4}>
                                                            <Paper elevation={1} sx={{ padding: 3, backgroundColor: colors.blue, color: colors.text }}>
                                                                <Typography variant='h6'>
                                                                    <CoronavirusIcon /> Symptoms
                                                                </Typography>
                                                                {medicalRecord.doc.symptoms.map((item, i) => (
                                                                    <Typography key={i}>
                                                                        - {item.symptom}
                                                                    </Typography>
                                                                ))}
                                                            </Paper>
                                                        </Grid>
                                                        <Grid item xs={12}>
                                                            <Paper elevation={1} sx={{ padding: 3, backgroundColor: colors.blue, color: colors.text }}>
                                                                <Typography variant='h6'>
                                                                    <MedicationIcon /> Prescriptions
                                                                </Typography>
                                                                <br />
                                                                <Grid container spacing={2}>
                                                                    {medicalRecord.doc.prescriptions.reverse().map((item, i) => (
                                                                        <Grid item xs={12} md={4} key={i}>
                                                                            <Paper elevation={1} sx={{ padding: 3 }}>
                                                                                <Typography >
                                                                                    Date Issued:{formatDate(item.createdAt)}
                                                                                </Typography>

                                                                                <br />
                                                                                {
                                                                                    item?.medicine?.map((med, j) => (
                                                                                        <Typography key={j} >
                                                                                            - {med.details}
                                                                                        </Typography>
                                                                                    ))}
                                                                                <br />
                                                                                <Avatar

                                                                                    alt={fullname(item.physician_id.name).toUpperCase()}
                                                                                    src={`/assets/img/${item.physician_id.photo}`}
                                                                                    sx={{
                                                                                        width: 80,
                                                                                        height: 80,
                                                                                        bgcolor: blue[500],
                                                                                        fontSize: 40,
                                                                                        boxShadow: 3,
                                                                                        margin: 'auto'
                                                                                    }}
                                                                                />
                                                                                <Typography align="center">
                                                                                    Prescribed by:
                                                                                    <br />
                                                                                    {fullname(item.physician_id.name)}
                                                                                </Typography>
                                                                                <Box
                                                                                    sx={{ display: "flex", justifyContent: 'center', alignItems: 'center' }}
                                                                                >
                                                                                    <Button
                                                                                        variant="outlined"
                                                                                        color="primary"
                                                                                        onClick={() => downloadPrescriptionRecord(item._id)}
                                                                                        size="small"
                                                                                        align="center"
                                                                                    >
                                                                                        <DownloadIcon />
                                                                                        Download
                                                                                    </Button>
                                                                                </Box>
                                                                            </Paper>
                                                                        </Grid>

                                                                    ))}
                                                                </Grid>
                                                            </Paper>
                                                        </Grid>
                                                    </Grid>
                                                )
                                                    : (
                                                        <Typography variant='h5'>
                                                            Medical Record of Patient Will Appear Here
                                                        </Typography>
                                                    )
                                            }

                                        </AccordionDetails>
                                    </Accordion>
                                ) : ("")
                            }
                        </Grid>
                        <Grid item xs={12} md={6} sx={{ paddingTop: 5, paddingBottom: 5 }}>
                            <Paper elevation={3} sx={{ padding: 3, height: '100%' }}>
                                {/* Address */}
                                <Typography variant="h6" gutterBottom component="div">
                                    Address
                                </Typography>
                                <List>
                                    {user.doc?.address?.map((item, i) => (
                                        <ListItem key={i}>
                                            <ListItemAvatar>
                                                <Avatar>
                                                    <HomeIcon />
                                                </Avatar>
                                            </ListItemAvatar>
                                            <ListItemText
                                                primary={`${item.house_number} ${item.street} ${item.barangay} ${item.municipality} ${item.city_province}`}
                                            />
                                        </ListItem>
                                    ))}

                                </List>
                                {/* Contact */}
                                <Typography variant="h6" gutterBottom component="div">
                                    Contact
                                </Typography>
                                <List>
                                    {user.doc?.contact?.map((item, i) => (
                                        <ListItem
                                            key={item.id}
                                        >
                                            <ListItemAvatar>
                                                <Avatar>
                                                    <PhoneIcon />
                                                </Avatar>
                                            </ListItemAvatar>
                                            <ListItemText
                                                primary={`${item.contact}`}
                                            />
                                        </ListItem>
                                    ))}
                                </List>
                            </Paper>
                        </Grid>
                        <Grid item xs={12} md={6} sx={{ paddingTop: 5, paddingBottom: 5 }}>
                            <Paper elevation={3} sx={{ padding: 3, height: '100%' }}>
                                <Typography variant="h6" gutterBottom component="div">
                                    Basic Info
                                </Typography>
                                <List>
                                    {basicInfo?.map((item, i) => (
                                        <ListItem key={i}>
                                            <ListItemAvatar>
                                                <Avatar>
                                                    {item.icon}
                                                </Avatar>
                                            </ListItemAvatar>
                                            <ListItemText
                                                primary={item.text}
                                            />
                                        </ListItem>
                                    ))}

                                </List>
                            </Paper>
                        </Grid>
                    </Grid>

                </Container>
            </>
        )
    }
    return 'Loading...'
}

export default ViewUser